:root {
  --navColor: #ffffff;
  --reverseToNavColor: #000000;
  --backColor: #f0f2f5;
  --containerColor: #3b55d9;
  --container2Color: #eeeeee;

  --testColor1: #febebe;
  --testColor2: #bbb;
  --textColor1: #2f2f2f;
  --textColor2: #ffffff;
  --testColor3: #89cff0;
  --blueColor: #0084ff;
  --shadow1: rgba(0, 0, 0, 0.1);
  --shadow2: rgba(0, 0, 0, 0.2);
  --shadow3: rgba(0, 0, 0, 0.3);
  --typicalShadow: rgba(0, 0, 0, 0.5);

  --shadowInset: rgba(255, 255, 255, 0.5);
  --hoverColor: #e4e6e9;
  --borderColor: #ddd;
  --color: rgb(71, 72, 72);

  --textGrey: #7e7f81;
  --lightGrey: #d6d9dfdc;
  --lightBlue: rgba(0, 132, 255, 1);

  --iconsBackColor: #f0f2f5;
  --scrollColor: #2f2f2f;
}

.very-small-font {
  font-size: 0.5rem;
}
.small-font {
  font-size: 0.8rem;
}
.medium-font {
  font-size: 1.2rem;
}
.large-font {
  font-size: 1.6rem;
}
.very-large-font {
  font-size: 2rem;
}

.bold {
  font-weight: bold;
}
.bolder {
  font-weight: bolder;
}

.shadow-around {
  box-shadow: 0 0px 0.5rem 0 var(--shadow3);
}

.big-shadow-around {
  box-shadow: 0 12px 28px 0 var(--shadow2), 0 2px 4px 0 var(--shadow1),
    inset 0 0 0 1px var(--shadowInset);
}

.big-shadow-notBottom {
  box-shadow: 0 12px 28px 0 var(--shadow2), 0 2px 4px 0 var(--shadow1),
    -12px 0 0 0 var(--shadowInset), 12px 0 0 0 var(--shadowInset);
}

.big-shadow-notTop {
  box-shadow: 0 2px 4px 0 var(--shadow1), 0 0 0 1px var(--shadowInset),
    12px 0 28px 0 var(--shadow2), -12px 0 28px 0 var(--shadow2);
}

.shadow-beneath {
  box-shadow: 0 0.05rem 0.2rem 0 var(--shadow2);
}
.shadow-above {
  box-shadow: 0 -0.05rem 0.2rem 0 var(--shadow2);
}

@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");

@import url("https://fonts.googleapis.com/css?family=Corben:700");

@media only screen and (min-width: 4000px) {
  html {
    font-size: 48px;
  }
}

@media only screen and (min-width: 2400px) and (max-width: 4000px) {
  html {
    font-size: 26px;
  }
}

@media only screen and (min-width: 1100px) and (max-width: 2400px) {
  html {
    font-size: 16px;
  }
}
@media only screen and (min-width: 700px) and (max-width: 1100px) {
  html {
    font-size: 10px;
  }
}

@media only screen and (min-width: 400px) and (max-width: 700px) {
  html {
    font-size: 10px;
  }
}
@media only screen and (max-width: 400px) {
  html {
    font-size: 8px;
  }
}

* {
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 0.5rem;
  border-radius: 0.5rem;
}
::-webkit-scrollbar-track {
  background: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollColor);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

h4 {
  margin: 0;
}

h1 {
  font-family: "Corben", cursive;
  font-size: 6rem;
  letter-spacing: 0.1rem;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.66);
}
h5 {
  margin: 1.5rem 0;
}

body {
  background-color: var(--backColor);
  color: var(--textColor1);
  font-size: 1.2rem;
  overflow-x: hidden;
}

button {
  background-color: var(--navColor);
  font-size: 1.2rem;
  border-radius: 1rem;
  cursor: pointer;
  padding: 0.5rem;
  transition: background-color 0.3s;
  color: var(--textColor1);
  border: none;
  outline: none;
  &:hover {
    background-color: var(--hoverColor);
  }
}
.my-input {
  font-size: inherit;
  border-radius: 10px;
  text-align: center;
  width: fit-content;
}
label {
  margin: 0% 0;
  text-align: center;
  display: flex;
  font-weight: 600;
}

.error {
  color: red;
  font-size: 1.5rem;
}

.App {
  text-align: center;
  position: relative;
  z-index: -1;
}
.landing-page {
  position: relative;
  min-height: 93.9vh;
  min-width: 100vw;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  z-index: -1;
}

.navbar-placeholder {
  display: flex;
  width: 100%;
  height: 6vh;
}

.file-input-image {
  height: 80%;
}

.modal-header {
  min-height: 3rem;
  width: 100%;
  border-bottom: 1px solid #999;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  position: relative;
  z-index: -1;
}
#root {
  position: relative;
  z-index: -1;
}

.show-text {
  margin-left: 0.5rem;
  color: red;
  cursor: pointer;
}

.erase-image {
  position: absolute;
  right: 0.2rem;
  top: 0.2rem;
  height: 2rem;
  aspect-ratio: 1;
  cursor: pointer;
}
.chat {
  .erase-image {
    top: 0.5rem;
    right: 0.5rem;
  }
}

.body {
  .element {
    img {
      vertical-align: middle;
      margin: auto auto;
      min-width: 30%;
      width: fit-content;
      height: fit-content;
    }
  }
}

.flexStart {
  display: flex;
  justify-content: flex-start;
}

.flexColumnLeft {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-column-center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.full-container {
  width: 100%;
  height: 100%;
}

.whole-page {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 15;
}

.profileImage {
  width: 2.4rem;
  height: 2.4rem;
  border-radius: 50%;
}

.header {
  width: calc(100% - 2rem);
  height: 10%;
  padding: 0 1rem;
  img {
    height: 100%;
    aspect-ratio: 1;
    border-radius: 50%;
  }
}
.body {
  width: 100%;
  height: 80%;
}
.footer {
  width: 100%;
  height: 10%;
}

.small-padding {
  padding: 0.5rem;
}
.medium-padding {
  padding: 1rem;
}
.pointer {
  cursor: pointer;
}

.nightMode {
  background-color: #2f2f2f;
  color: #fff;
  .navbar-placeholder {
    background-color: #2f2f2f;
  }
  .navbar {
    background-color: #2f2f2f;
  }
}

.elipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.left-align {
  text-align: left;
}

.hover-underline {
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}

.padding-1 {
  padding: 1rem;
}

.vertical-padding-1 {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.horizontal-padding-1 {
  padding-left: 1rem;
  padding-right: 1rem;
}

.gap-1 {
  gap: 1rem;
}

.pv-1 {
  padding: 1rem 0;
}

.ph-1 {
  padding: 0 1rem;
}

.mv-1 {
  margin: 1rem 0;
}

.mh-1 {
  margin: 0 1rem;
}

.hyperLink {
  color: var(--blueColor);
  cursor: pointer;
}

.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.auto-width {
  width: fit-content;
}

.mb-1 {
  margin-bottom: 1rem;
}

.skeleton-background {
  background-color: var(--lightGrey) !important;
}

.skeleton {
  opacity: 0.7;
  animation: skeleton-loading 1s linear infinite alternate;
}

.skeleton-text {
  opacity: 0.7;
  animation: skeleton-loading 1s linear infinite alternate;
  border-radius: 1rem;
}

.skeleton-circle {
  opacity: 0.7;
  animation: skeleton-loading 1s linear infinite alternate;
  border-radius: 50%;
}

.skeleton-text:last-child {
  margin-bottom: 0;
  width: 80%;
}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 70%);
  }

  100% {
    background-color: hsl(200, 20%, 95%);
  }
}
