@import "../../styles/mixins.scss";
.showFullImages {
  // position: fixed;
  // top: 0;
  // left: 0;
  // z-index: 9999;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  &-container {
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    &__close {
      position: absolute;
      top: 10px;
      left: 10px;
      height: 4rem;
      width: 4rem;
      font-size: 30px;
      cursor: pointer;
    }
    &__image {
      max-width: 100%;
      object-fit: contain;
      img {
        max-width: 100%;
        object-fit: contain;
      }
    }
  }
}

.scrollImages {
  position: absolute;
  top: 50%;
  padding: 0.5rem;
  border: none;
  cursor: pointer;

  background-size: cover;
  background-color: var(--borderColor);
  @include circle(1.5rem, height);
  padding: 1rem;
  transition: all 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 90%;
    height: 90%;
  }
  &:hover {
    background-color: var(--backColor);
  }
}
.scrollImages-left {
  left: 0.8rem;
}

.scrollImages-right {
  right: 0.8rem;
  transform: rotate(180deg);
}
